
import {IJData, ILResults} from '@/components/models';
import { defineComponent } from 'vue';

export default defineComponent({
  name: 'homeView',
  data(){
    return{
      videos: ["video-largo", "video-p3", "video-pool", "video-domino"],
      currIndex: 0,
      syntonize: [
        {
          at: "Teleuniverso Canal 29",
          url: "https://teleuniversotv.com/en-vivo/",
          icon: "teleuniverso.svg",
        },
        {
          at: "CERTV Canal 4",
          url: "https://emisoradominicana.net/television/certv-canal-4/",
          icon: "canal4rd.svg",
        },
        {
          at: "Boreal",
          url: "https://canalesdominicanos.live/boreal-television/",
          icon: "borealtv.png",
          width: '150px',
          class:'adjustLeft'
        },
        {
          at: "Canal 17, 76",
          icon: "claro.svg",
          width: '150px',
          class:'adjustLeft'

        },
        {
          at: "Canal 17",
          icon: "altice.svg",
        },
      ],
      listen: [
        {
          at: "Quisqueya FM",
          url: "https://radios.com.do/quisqueya-santo-domingo/",
        },
        {
          at: "Dominicana FM",
          url: "http://emisorasdominicanasonline.com/dominicana-fm",
        },
        {
          at: "Radio Santo Domingo",
        },
        {
          at: "Z101",
          url: "http://emisorasdominicanasonline.com/la-z101-fm",
        },
        {
          at: "AM 99.9",
        },
        {
          at: "Natura",
          url: "https://radios.com.do/natura-fm/",
        },
        {
          at: "Grupo Medrano",
          subAt: "(Super K 100.7, Suave 107.3)",
          url: "http://emisorasdominicanasonline.com/super-k-1007-fm",
        },
        {
          at: "Radio Cadena Hispanoamericana",
          subAt: "Rumba 98.5 FM, Premium 90.1 FM",
          url: "http://emisorasdominicanasonline.com/rumba-985-fm",
        },
      ],
      jackpotData: {} as IJData,
      lastJackpotResult: {results: ['']} as ILResults,


      NRT: {results: ['']} as ILResults, //LOTERIA REAL
      NNM: {results: ['']} as ILResults, //NUEVA YOL REAL
      NLP: {results: ['']} as ILResults, //LOTO POOL
      NTF: {results: ['']} as ILResults, //TU FECHA REAL
      NP4: {results: ['']} as ILResults, //PEGA 4

      NNT: {results: ['']} as ILResults, //NACIONAL TARDE
      NPR: {results: ['']} as ILResults, //LA PRIMERA
      NL: {results: ['']} as ILResults, //QUINIELA PALE
      NN: {results: ['']} as ILResults, //NACIONAL NOCHE

      bolos: ["VE", "RO", "AM", "AZ"],
      NNMApple: ["V", "R", "A"],

      resDate: '',

      //QOL
      hasLoaded: false,
    };
  },
  methods:{
    playNext(){
      if(this.currIndex == 3){
        this.currIndex = 0;
        return;
      }
      this.currIndex++;
    },
    async getJackpot(){
      const res = await this.$api.get("/results/jackpot/current");

      if(res.success){
        if(res.data && res.data.length > 0){
          const { lottery, playValue, accumulated, suffix, nextDate } = res.data[0];
          
          const pValues = playValue.split("-"),
                jackpots = accumulated.split("-"),
               dateText = new Date(nextDate).toLocaleString('default', {month: 'long', weekday: 'long', day:'numeric', year: 'numeric'});

          this.jackpotData = {
            lottery,
            suffix,
            nextDate: dateText.toUpperCase().replace(',', ''),
            jackpot1: jackpots[0],
            jackpot2: jackpots[1],
            pValue1: pValues[0],
            pValue2: pValues[1],
          }

        }
      }

    }, 

    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    async getLimitedResults(lottery: string, limit: number, dateToText = false): Promise<any> {
      const res = await this.$api.get("/results/"+lottery+"/"+limit);
      if(res.success){
        if(res.data && res.data.length > 0){

          const arr: ILResults[] = [];

          if(res.data.length > 1){
            // eslint-disable-next-line @typescript-eslint/no-explicit-any
            res.data.forEach((r: any) => {
              
              const { lottery, date, results, sorteo } = r;

              if(this.resDate == '' && lottery != 'LLR'){
                
                const d = new Date(date);
                this.resDate = d.toLocaleString('default', {month: 'long', weekday: 'long', day:'numeric', year: 'numeric'});

              }

              const dateText = new Date(date).toLocaleString('default', {month: 'long', weekday: 'long', day:'numeric', year: 'numeric'});
              
              arr.push({
                lottery, 
                results: results.split("-"),
                sorteo,
                date: dateToText ? dateText.toUpperCase().replace(',', '') : this.$utils.formatDate(date) // dateText.toUpperCase().replace(',', '')
              }) 

            });
          }else{

            const { lottery, date, results, sorteo } = res.data[0];

              if(this.resDate == '' && lottery != 'LLR'){
                
                const d = new Date(date);
                this.resDate = d.toLocaleString('default', {month: 'long', weekday: 'long', day:'numeric', year: 'numeric'});
              }
              const dateText = new Date(date).toLocaleString('default', {month: 'long', weekday: 'long', day:'numeric', year: 'numeric'});

              return {
                lottery, 
                results: results.split("-"),
                sorteo,
                date: dateToText ? dateText.toUpperCase().replace(',', '') : this.$utils.formatDate(date) // dateText.toUpperCase().replace(',', '')
              } 

          }

          return arr;

        }
      }

      return {results: ['']};

    },

    async getResults(){
      this.lastJackpotResult = await this.getLimitedResults('LLR', 1, true);

      this.NRT = await this.getLimitedResults('NRT', 1);
      this.NNM = await this.getLimitedResults('NNM', 1);
      this.NLP = await this.getLimitedResults('NLP', 1);
      this.NP4 = await this.getLimitedResults('NP4', 1);
      this.NTF = await this.getLimitedResults('NTF', 1);

      //Other lotteries
      this.NNT = await this.getLimitedResults('NNT', 1);
      this.NPR = await this.getLimitedResults('NPR', 1);
      this.NL = await this.getLimitedResults('NL', 1);
      this.NN = await this.getLimitedResults('NN', 1);

      this.hasLoaded = true;

    },

    getColorRegion(n: number){
      return n > 1 && n <= 25 ? 0 :
             n > 25 && n <= 50 ? 1 :
             n > 50 && n <= 75 ? 2 :
             n > 75 || n == 0 ? 3 : 0;
    }

  },
  mounted(){
    this.getJackpot();
    this.getResults();
  }
});
